table {
  margin-top: 12px;
  border-collapse: collapse;
  display: table;
  border-collapse: separate;
  box-sizing: border-box;
  text-indent: initial;
  border-spacing: 2px;
  border-color: grey;
}

.table {
  width: 100%;
  margin-bottom: 1rem;
  color: #212529;
  margin-left: 10px;
}
thead {
  display: table-header-group;
  vertical-align: middle;
  border-color: inherit;
}

.table-striped tbody tr:nth-of-type(odd) {
  background-color: rgba(143, 196, 240, 0.562);
}
tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
}
.table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
}
.table td,
.table th {
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid #dee2e6;
}
th {
  text-align: inherit;
  display: table-cell;
  vertical-align: inherit;
  font-weight: bold;
  text-align: -internal-center;
}
tbody {
  display: table-row-group;
  vertical-align: middle;
  border-color: inherit;
}

